<template>
  <section class="index_slogan position-relative parallax_box">
    <div class='layer parallax' data-depth='0.2' :style="{ backgroundImage: 'url(' + './img/index_slogan.jpg' + ')' }"></div>
      <div class="container d-flex align-items-center h-100">
        <div class="px-lg-5">
          <div class="info overflow-hidden">
            <div>嚴選美味．手工烘培</div>
            <div>用味覺記憶生命中的幸福時光</div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  mounted () {
    this.scrollAnimation()
  },
  methods: {
    scrollAnimation () {
      const tl = this.gsap.timeline({
        scrollTrigger: {
          trigger: 'body',
          start: 'top',
          scrub: 1
        }
      })
      this.gsap.utils.toArray('.parallax').forEach(layer => {
        const depth = layer.dataset.depth
        const movement = -(layer.offsetHeight * depth)
        tl.to(layer, { y: movement, ease: 'none' }, 0)
      })
      this.gsap.timeline({
        scrollTrigger: {
          trigger: '.index_slogan',
          start: 'top center'
        }
      }).from('.index_slogan .info > div', {
        duration: 1,
        y: 50,
        opacity: 0,
        stagger: 0.3
      })
    }
  }
}
</script>
