<template>
  <div class="home overflow-hidden">
    <div class="bread-crumbs">
      <ol itemscope="" itemtype="http://schema.org/breadcrumbList" id="breadcrumbs">
        <li itemprop="itemListElement" itemscope="" itemtype="http://schema.org/ListItem">
          <router-link to="/index" itemprop="item" title="劉記花生">
            <span itemprop="name">首頁</span>
          </router-link>
          <meta itemprop="position" :content="1">
        </li>
      </ol>
    </div>
    <SwiperCarouselVue />
    <indexBg />
    <indexArt />
    <indexBg2 />
    <indexPrd />
  </div>
</template>

<script>
import SwiperCarouselVue from '../components/SwiperCarousel.vue'
import indexBg from '../components/indexBg.vue'
import indexArt from '../components/indexArt.vue'
import indexBg2 from '../components/indexBg2.vue'
import indexPrd from '../components/indexPrd.vue'
export default {
  name: 'Home',
  components: {
    SwiperCarouselVue,
    indexBg,
    indexArt,
    indexBg2,
    indexPrd
  },
  mounted () {
    const index_ = this.gsap.timeline({})
    index_.from('.banner', {
      duration: 1,
      x: 300,
      opacity: 0,
      stagger: 0.3
    })
    index_.from('.banner_info', {
      duration: 1,
      opacity: 0,
      stagger: 0.3,
      delay: -0.2
    })
    index_.from('header .row.wrap', {
      duration: 0.8,
      y: -50,
      opacity: 0,
      stagger: 0.3,
      delay: -0.5
    })
    index_.from('.index_bg', {
      duration: 1,
      y: 50,
      opacity: 0,
      stagger: 0.3,
      delay: -1
    })
    const bannerInfo = this.gsap.timeline({
      scrollTrigger: {
        trigger: '.banner_out',
        start: 'top'
      }
    })
    bannerInfo.to('.banner_info', {
      duration: 1.15,
      y: -100,
      opacity: 0,
      stagger: 0.3
    })
  }
}
</script>
