<template>
  <section class="index_bg position-relative mt-lg-5 mt-4 parallax_box">
    <div class='layer parallax' data-depth='0.20' :style="{ backgroundImage: 'url(' + './img/index_bgimg.jpg' + ')' }"></div>
    <div class="logo_vertical">
      <img src="@/assets/img/logo_vertical.svg" alt="" class="svg">
    </div>
  </section>
</template>

<script>
export default {
  mounted () {
    this.scrollAnimation()
  },
  methods: {
    scrollAnimation () {
      const tl = this.gsap.timeline({
        scrollTrigger: {
          trigger: 'body',
          start: '10% 10%',
          end: '70% 50%',
          scrub: 1
        }
      })
      this.gsap.utils.toArray('.parallax').forEach(layer => {
        const depth = layer.dataset.depth
        const movement = -(layer.offsetHeight * depth)
        tl.to(layer, { y: movement, ease: 'none' }, 0)
      })

      this.gsap.timeline({
        scrollTrigger: {
          trigger: '.index_bg',
          start: '40% 90%',
          end: '50% 60%',
          scrub: 1
        }
      }).from('.logo_vertical', {
        duration: 2,
        y: 200,
        opacity: 0,
        stagger: 0.3
      })
    }
  }
}
</script>

<style scoped>
h2 {
  margin-top: 200px;
  margin-bottom: 200px;
}
</style>
