<template>
  <section class="index_news position-relative pt-5 overflow-hidden">
      <div class="container pb-5 position-relative z-index-1">
        <div class="px-lg-5">
          <h2 class="text-center"><span>最新消息</span></h2>
          <div class="row news-list mx-n2">
            <div class="col-lg-4 col-sm-6 mb-lg-5 mb-4 px-2 item" v-for="item in articles" :key="item">
              <router-link :to="`news/${item.id}`">
                <div class="inner bg-white p-4">
                  <div class="img mb-3">
                    <img :src="item.image" :alt="item.title" class="w-100" />
                  </div>
                  <div class="title mb-2">
                    {{item.title}}
                  </div>
                  <div class="info mb-2">
                    {{item.description}}...
                    <span>閱讀更多</span>
                  </div>
                  <div class="date">
                    <img src="@/assets/img/clock.svg" alt="" class="me-2" />{{ $filters.date(item.create_at)}}
                  </div>
                </div>
              </router-link>
            </div>
          </div>
          <div class="text-center pt-3 pt-lg-0">
            <router-link to="/news" class="btn btn-outline-light mx-auto">更多消息</router-link>
          </div>
        </div>
      </div>
    </section>
</template>
<script>
export default {
  data () {
    return {
      articles: []
    }
  },
  mounted () {
    const api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/articles`
    this.$http.get(api)
      .then((res) => {
        if (res.data.success) {
          this.articles = res.data.articles.slice(0, 3)
        }
      })
      .then(() => {
        this.gsap.from('.item', {
          scrollTrigger: '.news-list',
          duration: 1.15,
          y: 50,
          opacity: 0,
          stagger: 0.3
        })
      })
  }
}
</script>
