<template>
  <section class="index_prd py-5 bg-white">
      <div class="container">
        <div class="px-lg-5">
          <h2 class="text-center mb-4"><span>商品介紹</span></h2>
          <ul
            class="nav nav-pills mb-4 mx-n2 justify-content-center justify-content-lg-start"
            id="pills-tab"
            role="tablist"
          >
            <li class="nav-item" role="presentation" @click.prevent="tabActive = ''">
              <button
                class="nav-link"
                id="prd_01-tab"
                data-toggle="pill"
                data-target="#prd_01"
                type="button"
                role="tab"
                aria-controls="prd_01"
                aria-selected="true"
                :class="{ active: tabActive === '' }"
              >
                全部商品
              </button>
            </li>
            <li
              class="nav-item"
              role="presentation"
              v-for="item in classification"
              :key="item"
              @click.prevent="tabActive = item"
            >
              <button
                class="nav-link"
                id="prd_02-tab"
                data-toggle="pill"
                data-target="#prd_02"
                type="button"
                role="tab"
                aria-controls="prd_02"
                aria-selected="false"
                :class="{ active: tabActive === item }"
              >
                {{ item }}
              </button>
            </li>
          </ul>
          <div class="tab-content mb-4" id="pills-tabContent">
            <div class="tab-pane fade show active" role="tabpanel">
              <ProductList :list="filterData" :page="currentPage"></ProductList>
              <div class="text-center pt-3 pt-lg-0">
                <router-link :to="`/products/${ tabActive }`" class="btn btn-outline-primary mx-auto more">
                  更多商品
                </router-link>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
</template>
<script>
import ProductList from '../components/ProductList.vue'
export default {
  components: {
    ProductList
  },
  data () {
    return {
      products: [],
      classification: [],
      tabActive: '',
      currentPage: 1
    }
  },
  computed: {
    filterData () {
      const vm = this
      if (this.tabActive === '') {
        const newRecords = []
        this.products.forEach((item, i) => {
          if (i % 6 === 0) {
            newRecords.push([])
          }
          const page = parseInt(i / 6)
          newRecords[page].push(item)
        })
        return newRecords.slice(0, 1)
      } else {
        var newProducts = []
        this.products.forEach(function (item) {
          if (item.category === vm.tabActive) {
            newProducts.push(item)
          }
        })
        const newRecords = []
        newProducts.forEach((item, i) => {
          if (i % 6 === 0) {
            newRecords.push([])
          }
          const page = parseInt(i / 6)
          newRecords[page].push(item)
        })
        return newRecords.slice(0, 1)
      }
    }
  },
  mounted () {
    const api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/products/all`
    this.$http.get(api)
      .then((res) => {
        if (res.data.success) {
          const vm = this
          this.products = res.data.products
          let set = new Set()
          set = this.products.filter(function (item) {
            if (!set.has(item.category)) {
              return set.add(item.category)
            }
          })
          set.filter(function (item) {
            return vm.classification.push(item.category)
          })
        }
      })
      .then(() => {
        this.gsap.from('.prd-list > div', {
          scrollTrigger: '.prd-list',
          duration: 1.15,
          y: 50,
          opacity: 0,
          stagger: 0.3
        })
      })
  }
}
</script>
